.nav-color{background-color:#fff;z-index:2;font-size:19px}.text-boldest{font-weight:900}.color-customBlue{color:#091292 !important}.color-customBlueLight{color:#8d81a5 !important}.color-customBlueLighter{color:#a299b6}.bg-customBlue{background-color:#091292 !important}.b-logo{width:40px;background-color:#071b4b;height:40px;text-align:center;font-size:28px;border-radius:10px}.navLink{min-width:100px}.navLink:hover{font-weight:bold}.skillsImg{width:80px;transition:width 0.15s;transition-timing-function:ease-in-out;cursor:pointer}.skillsImg:hover{width:90px;-webkit-filter:none;filter:none}.justify-content-evenly{-webkit-justify-content:space-evenly;justify-content:space-evenly}.proj-image{border-radius:20px}.proj-opacity{-webkit-animation:opacityChange 2s;animation:opacityChange 2s}.text-link,.text-link:hover{color:#fff;text-decoration:none}.contact-btns span{max-width:0;transition:max-width 1s;display:inline-block;vertical-align:top;white-space:nowrap;overflow:hidden}.contact-btns:hover span{max-width:7rem}.contact-btns:hover span::before{content:'|';padding-left:5px;padding-right:5px;color:#ffffff}.particle-container{position:relative;background-color:#fff;z-index:-1}.color-nameBlue{color:#000a94 !important}.iconCard{height:100px;width:90px}.contactImg{width:35%}.contactCard,.skill-icons-container{-webkit-animation:slidein 1.5s;animation:slidein 1.5s}.proj-slide{-webkit-animation:slidein 2s;animation:slidein 2s}.contactCard:hover{cursor:pointer;box-shadow:1px 1px 7px grey !important}.contact .card{border:none !important}.about-text{letter-spacing:1px;font-size:18px;color:#727070}.vertical-timeline::before{background:#bebebe !important}.about-me-block{font-family:Fira Sans, sans-serif;font-weight:700;font-size:5.5rem;line-height:120px;opacity:0.1;color:#727070;text-transform:uppercase;text-align:right}.timeline-work-icon{left:26px !important}.timeline-grad-icon{left:23px !important}.home-container{position:absolute;top:0;left:0;right:0;z-index:1}.resume-btn:hover{background-image:linear-gradient(to top right, #091292, #2631d4)}.bipin-signature{font-family:'Arizonia', cursive;font-size:2.7rem;color:#191e69}.bipin-signature:hover{color:#091292}.dashed-border{border-style:dashed}.programmer{width:50%}.page-not-found{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;-webkit-flex-flow:row wrap;flex-flow:row wrap;font-weight:bold;margin-top:13%}@-webkit-keyframes slidein{from{margin-top:10%;opacity:0}to{margin-top:0%;opacity:1}}@keyframes slidein{from{margin-top:10%;opacity:0}to{margin-top:0%;opacity:1}}@-webkit-keyframes slideout{from{margin-top:-2%;opacity:0}to{margin-top:0%;opacity:1}}@keyframes slideout{from{margin-top:-2%;opacity:0}to{margin-top:0%;opacity:1}}@-webkit-keyframes opacityChange{from{opacity:0}to{opacity:1}}@keyframes opacityChange{from{opacity:0}to{opacity:1}}@media only screen and (min-width: 320px) and (max-width: 1168px){.timeline-work-icon{left:16px !important}.timeline-grad-icon{left:13px !important}.programmer{width:100%}.page-not-found{margin-top:22%}}@media only screen and (min-width: 768px){.nav-slide{-webkit-animation:slideout 1.5s;animation:slideout 1.5s}}

